<template>
    <div>
        <div class="cropper-content">
            <input
                @change="openFile"
                ref="cropperInput"
                :id="id + 'File'"
                type='file'
                class="fileInput"/>
            <!--  -->
            <div
                v-for="(img, index) in imgs"
                :key="index"
                @click="openCropper(index)"
                class="cropper-btn-show">
                <div class="cropper-preview">
                    <img :src="img"/>
                    <i class="el-icon-circle-close cropper-preview-ope"
                        @click="deleteImg($event, index)">
                    </i>
                </div>
            </div>
            <!--  END -->
            <label
                v-show="showAddBtn"
                :for="id + 'File'"
                class="cropper-btn-add">
                <i class="el-icon-plus"></i>
            </label>
        </div>
        <!--  -->
        <div class="cropper-bomb-bg" v-if="showCropperBomb">
            <div class="cropper-bomb" :style="cropperBombStyle">
                <div class="cropper-bomb-title">
                    <i class="el-icon-picture-outline"></i> {{title}}
                    <el-button
                        @click="saveCropper"
                        type="success"
                        size="mini"
                        round
                        style="float: right; margin: 12px 0 0 0">
                        <i class="el-icon-check"></i>  
                    </el-button>
                    <el-button
                        v-show="customize && width === 0 && height === 0"
                        @click="closeCropper"
                        type="info"
                        size="mini"
                        round
                        style="float: right; margin: 12px 10px 0 0">
                        <i class="el-icon-close"></i>  
                    </el-button>
                </div>
                <!--  -->
                <div class="cropper-container" :style="cropperBgStyle">
                    <vue-cropper
                        ref="cropper"
                        :img="config.img"
                        :canScale="false"
                        :autoCrop="true"
                        :autoCropWidth="config.autoCropWidth"
                        :autoCropHeight="config.autoCropHeight"
                        :fixedBox="config.fixedBox"
                        :info="showInfo"
                        :fixed="config.fixed"
                        :fixedNumber="config.fixedNumber"
                        :full="true"
                        :outputSize="quality"
                        :outputType="type">
                    </vue-cropper>
                </div>
                <!--  END -->
            </div>
        </div>
        <!--  END -->
    </div>
</template>

<script>
    import VueCropper from 'vue-cropper';

    export default {
        name: 'NewxCropper',
        data() {
            return {
                // vue-cropper 
                config: {
                    img: '', // base64
                    autoCropWidth: 300, // 
                    autoCropHeight: 300, // 
                    fixed: false, // 
                    fixedNumber: [1, 1], // 
                    fixedBox: false, // 
                    target: ''
                },
                imgs: [], // base64
                origins: [], // 
                showCropperBomb: false, // 
                cropperBgStyle: '', // 
                cropperBombStyle: '', // 
                showAddBtn: true // 
            }
        },
        props: {
            // 
            width: {
                type: Number,
                default: 0
            },
            // 
            height: {
                type: Number,
                default: 0
            },
            // 
            max: {
                type: Number,
                default: 1
            },
            // 
            title: {
                type: String,
                default: 'Picture Cropper'
            },
            // 
            showInfo: {
                type: Boolean,
                default: false
            },
            // 
            auto: {
                type: Boolean,
                default: true
            },
            // 
            data: {
                type: Array,
                default() {
                    return [];
                }
            },
            // 0.1 ~ 1
            quality: {
                type: Number,
                default: 1
            },
            // jpeg || png || webp
            type: {
                type: String,
                default: 'jpeg'
            },
            // 
            id: {
                type: String,
                default: 'cropper'
            },
            // 
            customize: {
                type: Boolean,
                default: true
            }
        },
        methods: {
            // 
            openFile() {
                if (this.imgs.length >= this.max) {
                    this.onError('');
                    return;
                }

                // 
                let file = this.$refs.cropperInput.files[0];
                document.getElementById(this.id + 'File').value = '';

                // 
                if(!/image\/\w+/.test(file.type)){
                    this.onError('');
                    return;
                }

                // 
                let reader = new FileReader();
                reader.readAsDataURL(file);
                reader.onload = (e) => {
                    // 
                    const originsLength = this.origins.push(e.target.result);

                    // 
                    this.imgs.push(e.target.result);

                    // 
                    if (this.auto) {
                        this.openCropper(originsLength - 1);
                    }
                };
            },
            // 
            openCropper(index) {
                const origin = this.origins[index];

                // 
                if (origin.indexOf('http://') >= 0) {
                    this.onError('');
                    return;
                }

                // 
                const clientHeight = document.body.clientHeight;

                // 
                const image = new Image;
                image.src = origin;
                image.crossOrigin = "*";
                image.onload = () => {
                    // let canvas = document.createElement('canvas');
                    // let context = canvas.getContext('2d');
                    // context.drawImage(image, 0, 0);
                    // const imageData = canvas.toDataURL('images/jpeg');

                    const imageWidth = image.width;
                    const imageHeight = image.height;
                    const radio = imageWidth / imageHeight;

                    if (imageWidth <= this.width || imageHeight <= this.height) {
                        this.onError('');
                        return;
                    }

                    //  60. 40. 40.
                    const maxHeight = clientHeight - 60 - 40 - 40;

                    // 
                    const bgHeight = imageHeight > maxHeight ? maxHeight : imageHeight;
                    const bgWidth = radio * bgHeight;

                    // 100%
                    this.cropperBgStyle = 'height: ' + bgHeight + 'px';

                    // 
                    this.cropperBombStyle = 'width: '+ (bgWidth + 40) + 'px'; // 40.

                    // 
                    if (this.width > 0) {
                        const cropWidth = bgWidth / (imageWidth / this.width);
                        this.config.autoCropWidth = cropWidth;
                    }
                    if (this.height > 0) {
                        const cropHeight = bgHeight / (imageHeight / this.height);
                        this.config.autoCropHeight = cropHeight;
                    }

                    // 
                    this.config.img = origin;
                    this.config.target = index;
                    this.showCropperBomb = true;
                };

                image.onerror = () => {
                    this.onError('');
                };
            },
            // 
            saveCropper() {
                // base64
                this.$refs.cropper.getCropData((data) => {
                    this.imgs[this.config.target] = data;
                });

                // blob -> base64
                // this.$refs.cropper.getCropBlob((data) => {
                //     var reader = new FileReader();
                //     reader.onload = (e) => {
                //         this.imgs[this.config.target] = e.target.result;
                //     };
                //     reader.readAsDataURL(data);
                // });

                this.showCropperBomb = false;
            },
            // 
            closeCropper() {
                this.showCropperBomb = false;
            },
            // 
            deleteImg(event, index) {
                // 
                if (event && event.stopPropagation) {
                    event.stopPropagation();
                } else {
                    window.event.cancelBubble = true;
                }
                this.imgs.splice(index, 1);
                this.origins.splice(index, 1);
            },
            // 
            onError(msg) {
                this.$emit('cropperErrorEvent', msg);
            },
            // 
            addBtnStatus() {
                this.showAddBtn = this.imgs.length >= this.max ? false : true;
            },
            // 
            init() {
                if (this.data.length !== this.origins.length && this.data.length !== this.imgs.length) {
                    this.origins = Object.assign([], this.data);
                    this.imgs = Object.assign([], this.data);
                }
            },
            // 
            updateFixedNumber() {
                if (this.width > 0 && this.height > 0) {
                    this.config.fixedNumber = [this.width, this.height];
                } else {
                    this.config.fixedNumber = [];
                }
            }
        },
        mounted() {
            /*
             |  Props Description
             |-------------------------------|
             |  width     =>             |
             |  height    =>             |
             |  customize =>             |
             |-------------------------------|
             |  Program   =>  P2  P3  P1  P1 |
             |-------------------------------|
             |  P1.                   |
             |  P2.                   |
             |  P3.                     |
             |-------------------------------|
            */
            if (this.customize) {
                if (this.width > 0 && this.height > 0) {
                    // P2.
                    this.config.fixed = true;
                } else {
                    // P3.
                    this.config.fixed = false;
                }
                this.config.fixedBox = false;
            } else {
                // P1.
                this.config.fixed = false;
                this.config.fixedBox = true;
            }
            this.updateFixedNumber(); // 

            this.init(); // 
            this.addBtnStatus(); // 
        },
        watch: {
            imgs(val) {
                // 
                this.addBtnStatus();

                // 
                this.$emit('cropperDataEvent', val);
            },
            data() {
                this.init();
            },
            width(val) {
                if (val > 0) {
                    this.updateFixedNumber();
                }
            },
            height(val) {
                if (val > 0) {
                    this.updateFixedNumber();
                }
            }
        },
        components: { vueCropper: VueCropper }
    }
</script>

<style scoped>
    /*  */
    .cropper-content {
        padding: 10px;
        /* border: 1px dashed #D8DCE5; */
        float: left;
    }
    .cropper-btn-add {
        float: left;
        width: 100px;
        height: 100px;
        border: 1px dashed silver;
        line-height: 100px;
        border-radius: 5px;
        cursor: pointer;
        text-align: center;
        font-size: 20px;
        color: silver;
    }
    .cropper-btn-add:hover {
        border: 1px dashed #409EFF;
        color: #409EFF;
    }
    .cropper-btn-show {
        float: left;
        margin-right: 10px;
    }
    .cropper-preview { /*  */
        height: 100px;
        cursor: pointer;
        position: relative;
    }
    .cropper-preview img {
        border-radius: 5px;
        height: 100%;
    }
    .cropper-preview-ope {
        color: red;
        font-size: 20px;
        position: absolute;
        top: 3px;
        right: 3px;
    }
    /*  END */

    /*  */
    .cropper-bomb-bg {
        position: fixed;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.5);
        z-index: 1;
    }
    .cropper-bomb {
        background: #fff;
        padding: 20px;
        margin: 20px auto;
        box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.1);
    }
    .cropper-bomb-title {
        width: 100%;
        height: 40px;
        line-height: 40px;
        text-align: left;
        font-size: 16px;
        font-family: 'Microsoft YaHei';
        margin-bottom: 20px;
    }
    .cropper-container { /*  */
        width: 100%;
    }
    /*  END */

    .fileInput {
        display: none;
    }
</style>



